import React, { useContext } from 'react';
import { ExpenseContext } from '../../user-context/Context';
import Transaction from './TransactionHistory'

const History = () => {
 const { transaction } = useContext(ExpenseContext);
 
 return  transaction.length ? (
  <div className="trans__history">
    <h2>History</h2>
    
    <ul>
     { transaction.map(data => { 
        return(
        <Transaction key={data.id} transaction={data} />
     )})}
    </ul>
  </div>
 ) : ( <p className="history"> You have no history </p> )
}

export default History
