export const Reduce = (state, action) => {
   switch (action.type) {
      case 'AddTransaction':
         return {
            transaction: [
               ...state.transaction,
               action.AddTrans]
         }
      case 'Delete':
         return {
            transaction: 
            state.transaction.filter(transaction => transaction.id !== action.id)
         }

      default:
         return { state }
   }
}