import React from 'react'
import { ExpenseContext } from '../../user-context/Context';
import { v4 as uuid } from 'uuid';
import FormVaild from '../Validation/FormVaild'

var d = new Date()
const AddTransaction = () => {
  const [data, setData] = React.useState('')
  const { error } = FormVaild(data);
  const { dispatch } = React.useContext(ExpenseContext)
  // const id = Math.floor(Math.random() * 10000);

  //Initialize Month
  const month = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'Sept', 'Otc', 'Nov', 'Dec'];

  //Data Initialize
  const date = month[d.getMonth()]
    + " " + d.getDate() 
    + ", " + d.getFullYear();


  //Submit Handler
  const onSubmitHandler = (e) => {
    e.preventDefault();
    const datas = {
      id: uuid(),
      text: data.title,
      amount: +data.amount,
      dates: date
    }
     dispatch({ type: 'AddTransaction', AddTrans: datas });
     document.getElementById("reset").reset();
     console.log(data)
     
  };

  //onChange Handler
  const onChangeHandler = (e) => {
    e.preventDefault();
    const { name, value } = e.target;
    setData({
      ...data,
      [name]: value
    });
  };

  return (
    <div className="add__transaction">
      <h3>Add your self</h3>
      <form id="reset" onSubmit={onSubmitHandler}>
        <input
          name="title"
          placeholder="Enter Title..."
          onChange={onChangeHandler}
          type="text"
        />
        {error.title && <p> {error.title} </p>}
        <span className="sign">N.B: Add -(Minus sign) for Expense</span>
        <input
          type="text"
          name="amount"
          placeholder="Enter amount...(Ex. -100)"
          onChange={onChangeHandler}
        />
        <button type="submit" className="add__btn">
          Add Transaction
     </button>
      </form>
    </div>
  )
}

export default AddTransaction;
