export default function FormVaild( values ) {
  
  let error = {}

  if ( values.title === '' ) {
    error.title = "Please enter expense name";
  }
  else if ( typeof values.title !== 'string' ) {
    error.title = "Please valid expense name";
  }
  if ( values.amount === '' ) {
    error.amount = "Please enter expense amount";
  }
  else if ( typeof values.amount !== 'number' ) {
    error.amount = "Input is not number";
  }
  return { error }
}
