import React from 'react'
import { ExpenseContext } from '../../user-context/Context';


 const TransactionHistory = ({ transaction }) => {
  const { dispatch } = React.useContext(ExpenseContext)
  const sign = transaction.amount > 0 ? "" : "-"; 

 return (
  <li 
  className= {transaction.amount > 0 ? "plus__trasaction" : "minus__trasaction"
  }>
      <span> { transaction.text } </span>
      <span> {sign} ${Math.abs(transaction.amount)} </span>
      <span> {transaction.dates} </span>
      <button 
      onClick={() => dispatch({type: "Delete", id: transaction.id})} 
      className="times">X</button>
    </li>

 )
}

export default TransactionHistory;