import React, {useContext} from 'react';
import { ExpenseContext } from '../../user-context/Context';


const Balanace = () => {
  const { transaction } = useContext(ExpenseContext);
  const amount = transaction.map(data => data.amount);
  const total = amount.reduce((amount, item) =>
               ( amount +=item ), 0).toFixed(2);

 return (
  <div className="balance">
    <h2>Your Balance</h2>
    <p className={total > 0 ? "plus" : "minus"}> ${total} </p>
  </div>
 )
}

export default Balanace
