import React from 'react'
import { ExpenseContext } from '../../user-context/Context'

const Expense = () => {
  const { transaction } = React.useContext(ExpenseContext);

  //All amount in array
  const amount = transaction.map(data => data.amount);

  //Income Calculation
  const Income = amount.filter(amount=>amount > 0)
                .reduce((total, item)=>(total += item),0)
                .toFixed(2);

  // Expense Calculation               
  const Expense = amount.filter(amount=>amount < 0)
                 .reduce((total, item)=>(total += item),0)
                 .toFixed(2);
 return (
  <div className="inc-expense">
    <div>
     <h4>Income</h4>
     <p className="plus">${Income} </p>
    </div>
    <div>
     <h4>Expense</h4>
     <p className="minus">${Math.abs(Expense)}</p>
    </div>
  </div>
 )
}

export default Expense
