import React, { createContext, useReducer, useEffect } from 'react'
import { Reduce } from './Reduce'

const LocalData = JSON.parse(localStorage.getItem('transaction'));

//Initialize Value
const Initialize = { transaction: LocalData ? LocalData : [] }

//Initiliaze Context
export const ExpenseContext = createContext(Initialize)
const Context = (props) => {

  const [state, dispatch] = useReducer(Reduce, Initialize);

  useEffect(() => {
    localStorage.setItem('transaction', JSON.stringify(state.transaction))
  }, [state])


  return (
    <ExpenseContext.Provider value={{ dispatch, transaction: state.transaction }}>
      {props.children}
    </ExpenseContext.Provider>
  )
}

export default Context;
